.main {
  width: 100%;
  height: 100%;
}
.main .right_top_title {
  width: 100%;
  color: #8CE6FE;
  font-size: 20px;
  line-height: 1;
  margin-top: 18px;
  text-align: center;
  font-weight: bold;
}
.main .right_top_content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: calc(100% - 30px);
  height: calc(100% - 40px);
}
.main .right_top_content .dv-capsule-chart {
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.main .right_top_content .dv-capsule-chart .label-column div:last-child {
  display: none;
}
.main .right_top_content .dv-capsule-chart .unit-text {
  display: none;
}
.main .right_top_content .dv-capsule-chart .unit-label {
  display: none;
}
