/*
  @省略
  @singleEllipses:单行省略号;
  @multipleEllipses:多行省略号;
*/
.singleEllipses {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.multipleEllipses {
  display: -webkit-box;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
}
